@import "Colors";
nav {
    width: 100%;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    background-color: $first;
    top: 0%;
    z-index: 10;
    > h1 {
        text-transform: uppercase;
    }
    > main {
        width: 70%;
        display: flex;
        justify-content: flex-end;
        gap: 1rem;
        > a {
            color: $dark;
            margin: 1rem;
            transition: .7s;
            &:hover {
                color: $third;
                transition: .5s;
                font-size: 1.2rem;
            }
        }
    }
}
